import { CartDataList, RemoveCartList, ChangChecked } from '@/api/cart';
import { getUUID } from '@/utils/uuid_token';
export default {
	// 
	namespaced: true,
	state: {
		// 
		cartDataList: [],
		uuid_token: getUUID(),
	},
	mutations: {
		cartDataObj(state, obj) {
			state.cartDataList = obj;
		},
	},
	actions: {
		// 
		async getCartDataList(context) {
			let result = await CartDataList();
			if (result.data.code === 200) {
				context.commit('cartDataObj', result.data.data);
			}
		},
		// 
		async removeCartList(context, skuId) {
			let result = await RemoveCartList(skuId);
			if (result.data.code === 200) {
				return 'true';
			} else {
				// 
				return Promise.reject(new Error('Failed to remove the item'));
			}
		},
		// 
		async upDataChecked(context, { skuId, isChecked }) {
			let result = await ChangChecked(skuId, isChecked);
			if (result.data.code === 200) {
				return 'true';
			} else {
				return Promise.reject(new Error('Failed to switch status'));
			}
		},
	},
	getters: {
		cartDataList(state) {
			return state.cartDataList[0] || {};
		},
	},
};
