import { loginRequest, getUserInfo, quit } from '@/api/login';
export default {
	// 
	namespaced: true,
	state: {
		//  token
		token: localStorage.getItem('token'),
		// 
		userInfo: {},
		// 
		exceptionData: '',
	},
	mutations: {
		USERLOGIN(state, data) {
			state.token = data;
		},
		USERINFO(state, data) {
			state.userInfo = data;
		},
		LOGOUT(state) {
			state.token = '';
			state.userInfo = {};
		},
		addExceptionData(state, num) {
			state.exceptionData = num;
		},
	},
	actions: {
		// 
		async userLogin(context, data) {
			let result = await loginRequest(data);
			if (result.data.code === 200) {
				context.commit('USERLOGIN', result.data.data.token);
				//  token 
				localStorage.setItem('token', result.data.data.token);
				return 'true';
			} else {
				return Promise.reject(new Error(result.data.message));
			}
		},
		// 
		async userInfo(context) {
			let result = await getUserInfo();
			if (result.data.code === 200) {
				context.commit('USERINFO', result.data.data);
				return true;
			} else {
				// 
				context.commit('addExceptionData', result.data.code);
				return Promise.reject(new Error(result.data.code));
			}
		},
		// 
		async userLogOut(context) {
			//  token 
			let result = await quit();
			if (result.data.code === 200) {
				// mutations
				context.commit('LOGOUT');
				return 'true';
			} else {
				return Promise.reject(new Error(result.data.message));
			}
		},
	},
};
