<template>
	<div class="register-container">
		<!--  -->
		<div class="register">
			<h3>
				
				<span class="go"> <router-link to="/login"></router-link> </span>
			</h3>
			<div class="content">
				<label>:</label>
				<input type="text" placeholder="" v-model.number="phone" @keydown="verifyPhone" />
				<span class="error-msg" v-show="errorPhone"> +86</span>
			</div>
			<div class="content">
				<label>:</label>
				<input type="text" placeholder="" v-model.number="code" @keydown="verifyCode" />
				<button @click="sendCode" class="getCode"></button><span>{{ Captcha }}</span>
				<span class="error-msg" v-show="errorCode"></span>
			</div>
			<div class="content">
				<label>:</label>
				<input type="text" placeholder="" v-model="password" @keydown="verifyPassword" />
				<span class="error-msg" v-show="errorPassword">6~16</span>
			</div>
			<div class="content">
				<label>:</label>
				<input type="text" placeholder="" v-model="confirmThePassword" @keydown="verifyConfirmThePassword" />
				<span class="error-msg" v-show="errorConfirmThePassword"></span>
			</div>
			<div class="controls">
				<input name="m1" type="checkbox" :checked="agreement" @click="verifyAgreement" />
				<span></span>
				<span class="error-msg" v-show="errorAgreement"></span>
			</div>
			<div class="btn">
				<button @click="enroll"></button>
			</div>
		</div>

		<!--  -->
		<div class="copyright">
			<ul>
				<li></li>
				<li></li>
				<li></li>
				<li></li>
				<li></li>
				<li></li>
				<li></li>
				<li></li>
			</ul>
			<div class="address">6</div>
			<div class="beian">ICP19006430</div>
		</div>
	</div>
</template>

<script>
// lodash 
import debounce from 'lodash/debounce';
export default {
	name: 'AppRegister',
	data() {
		return {
			// 
			// 
			phone: '',
			// 
			code: '',
			// 
			password: '',
			// 
			confirmThePassword: '',
			// 
			agreement: false,

			// 
			// 
			errorPhone: true,
			// 
			errorCode: true,
			// 
			errorPassword: true,
			// 
			errorConfirmThePassword: true,
			// 
			errorAgreement: true,

			// 
			Captcha: '',
		};
	},
	computed: {
		isEnroll() {
			if (!this.errorPhone && !this.errorCode && !this.errorPassword && !this.errorConfirmThePassword && !this.errorAgreement) {
				return true;
			} else {
				return false;
			}
		},
	},
	methods: {
		// 
		async sendCode() {
			if (!this.errorPhone) {
				await this.$store.dispatch('register/getCode', this.phone);
				// this.code = this.$store.state.register.code;
				this.Captcha = this.$store.state.register.code;
			} else {
				this.$message({
					message: '',
					type: 'warning',
				});
			}
		},
		// 
		async enroll() {
			if (this.isEnroll) {
				try {
					const { phone, password, code } = this;
					await this.$store.dispatch('register/SignUpNow', { phone, password, code });
					this.$router.push('/login');
				} catch (error) {
					this.$message({
						message: error,
						type: 'error',
					});
				}
			} else {
				this.$message({
					message: '',
					type: 'warning',
				});
			}
		},
		// 
		verifyPhone: debounce(function () {
			let rge = /^(?:(?:\+|00)86)?1[3-9]\d{9}$/;
			if (!rge.test(this.phone)) {
				this.errorPhone = true;
			} else {
				this.errorPhone = false;
			}
		}, 500),
		// 
		verifyCode: debounce(function () {
			let rge = /^\d{6}$/;
			console.log(rge.test(this.code));
			if (!rge.test(this.code)) {
				this.errorCode = true;
			} else {
				this.errorCode = false;
			}
		}, 500),
		// 
		verifyPassword: debounce(function () {
			let rge = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[\S]{6,16}/;
			if (!rge.test(this.password)) {
				this.errorPassword = true;
			} else {
				this.errorPassword = false;
			}
		}, 500),
		// 
		verifyConfirmThePassword: debounce(function () {
			if (this.confirmThePassword !== this.password) {
				this.errorConfirmThePassword = true;
			} else {
				this.errorConfirmThePassword = false;
			}
		}, 500),
		// 
		verifyAgreement() {
			this.agreement = !this.agreement;
			if (!this.agreement) {
				this.errorAgreement = true;
			} else {
				this.errorAgreement = false;
			}
		},
	},
};
</script>

