<template>
	<header class="header">
		<!--  -->
		<div class="top">
			<div class="container">
				<div class="loginList">
					<p></p>
					<p v-if="!userName">
						<span></span>
						<router-link to="/login"></router-link>
						<router-link to="/register" class="register"></router-link>
					</p>
					<p v-else>
						<a>{{ userName }}</a>
						<span>&nbsp;&nbsp;</span>
						<a href="javascript:;" @click="signOut"></a>
					</p>
				</div>
				<div class="typeList">
					<!-- <router-link to="/center"></router-link> -->
					<a href="javascript:" @click="myOrder"></a>
					<router-link to="/shoppingcart"></router-link>
					<a href="javascript:;"></a>
					<a href="javascript:;"></a>
					<a href="javascript:;"></a>
					<a href="javascript:;"></a>
					<a href="javascript:;"></a>
					<a href="javascript:;"></a>
				</div>
			</div>
		</div>
		<!-- -->
		<div class="bottom">
			<h1 class="logoArea">
				<router-link class="logo" title="" to="/home">
					<img src="../../assets/logo.png" alt="" />
				</router-link>
			</h1>
			<div class="searchArea">
				<form action="" class="searchForm">
					<input type="text" id="autocomplete" class="input-error input-xxlarge" @keyup.enter="goSearch" v-model="value" />
					<button class="sui-btn btn-xlarge btn-danger" @click="goSearch" type="button"></button>
				</form>
			</div>
		</div>
	</header>
</template>

<script>
export default {
	name: 'AppHeader',
	data() {
		return {
			// input
			value: '',
		};
	},
	computed: {
		userName() {
			return this.$store.state.login.userInfo.name;
		},
	},
	methods: {
		goSearch() {
			// query this.$route.query 
			if (this.$route.query) {
				let location = { name: 'search', params: { keyword: this.value || undefined } };
				// query
				location.query = this.$route.query;
				this.$router.push(location);
			}
		},
		// 
		signOut() {
			this.$confirm('?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning',
			})
				.then(async () => {
					try {
						// 
						await this.$store.dispatch('login/userLogOut');
						//  token
						localStorage.removeItem('token');
						// 
						this.$router.push('/login');
						this.$message({
							message: '',
							type: 'success',
						});
					} catch (error) {
						this.$message({
							message: error,
							type: 'error',
						});
					}
				})
				.catch(() => {
					this.$message({
						type: 'info',
						message: '',
					});
				});
		},
		// 
		async myOrder() {
			try {
				await this.$store.dispatch('center/getMyOrderDataList', { page: 1, limit: 4 });
				this.$router.push('/center');
			} catch (error) {
				if (error.message === '208') {
					this.$confirm('', '', {
						confirmButtonText: '',
						cancelButtonText: '',
						type: 'warning',
					})
						.then(() => {
							this.$router.push('/login');
						})
						.catch(() => {
							this.$message({
								type: 'info',
								message: '',
							});
						});
				}
			}
		},
	},
	mounted() {
		this.$bus.$on('clearValue', () => {
			this.value = '';
		});
	},
};
</script>

<style scoped>
.header > .top {
	background-color: #eaeaea;
	height: 30px;
	line-height: 30px;
}
.header > .top .container {
	width: 1200px;
	margin: 0 auto;
	overflow: hidden;
}
.header > .top .container .loginList {
	float: left;
}
.header > .top .container .loginList p {
	float: left;
	margin-right: 10px;
}
.header > .top .container .loginList p .register {
	border-left: 1px solid #b3aeae;
	padding: 0 5px;
	margin-left: 5px;
}
.header > .top .container .typeList {
	float: right;
}
.header > .top .container .typeList a {
	padding: 0 10px;
}
.header > .top .container .typeList a + a {
	border-left: 1px solid #b3aeae;
}
.header > .bottom {
	width: 1200px;
	margin: 0 auto;
	overflow: hidden;
}
.header > .bottom .logoArea {
	float: left;
}
.header > .bottom .logoArea .logo img {
	width: 175px;
	margin: 25px 45px;
}
.header > .bottom .searchArea {
	float: right;
	margin-top: 35px;
}
.header > .bottom .searchArea .searchForm {
	overflow: hidden;
}
.header > .bottom .searchArea .searchForm input {
	box-sizing: border-box;
	width: 490px;
	height: 32px;
	padding: 0px 4px;
	border: 2px solid #ea4a36;
	float: left;
}
.header > .bottom .searchArea .searchForm input:focus {
	outline: none;
}
.header > .bottom .searchArea .searchForm button {
	height: 32px;
	width: 68px;
	background-color: #ea4a36;
	border: none;
	color: #fff;
	float: left;
	cursor: pointer;
}
.header > .bottom .searchArea .searchForm button:focus {
	outline: none;
}
</style>
