import Vue from 'vue';
// VueRouter
import VueRouter from 'vue-router';
// VueRouter
Vue.use(VueRouter);
//  store vuex)
import store from '@/store';
// 
import AppHome from '@/pages/Home';
import AppLogin from '@/pages/Login';
import AppSearch from '@/pages/Search';
import AppRegister from '@/pages/Register';
import AppDetail from '@/pages/Detail';
import ShoppingCart from '@/pages/ShoppingCart';
import trade from '@/pages/Trade';
import Pay from '@/pages/Pay';
import PaySuccess from '@/pages/PaySuccess';
import Center from '@/pages/Center';

// 
const RouterPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(to) {
	return RouterPush.call(this, to).catch(err => err);
};

// 
const router = new VueRouter({
	routes: [
		// 
		{
			path: '/home',
			component: AppHome,
			meta: {
				isShowFooter: true,
				title: '-',
			},
		},
		// 
		{
			// 

			// params
			// 

			// params
			// undefined
			name: 'search',
			path: '/search/:keyword?',
			component: AppSearch,
			meta: {
				isShowFooter: true,
				title: '-',
			},
		},
		// 
		{
			path: '/login',
			component: AppLogin,
			meta: {
				isShowFooter: false,
				title: '-',
			},
		},
		// 
		{
			path: '/register',
			component: AppRegister,
			meta: {
				isShowFooter: false,
				title: '-',
			},
		},
		// 
		{
			name: 'detail',
			path: '/detail/:skuId',
			component: AppDetail,
			meta: {
				isShowFooter: true,
			},
		},
		// 
		{
			path: '/shoppingcart',
			component: ShoppingCart,
			meta: {
				isShowFooter: true,
				title: '-',
			},
		},
		// 
		{
			path: '/trade',
			component: trade,
			meta: {
				isShowFooter: true,
				title: '-',
			},
		},
		// 
		{
			path: '/pay',
			component: Pay,
			meta: {
				isShowFooter: true,
				title: '-',
			},
		},
		// 
		{
			path: '/paysuccess',
			component: PaySuccess,
			meta: {
				isShowFooter: true,
				title: '-',
			},
		},
		// 
		{
			path: '/center',
			component: Center,
			meta: {
				isShowFooter: true,
				title: '-',
			},
		},
		// 
		{
			path: '*',
			redirect: '/home',
		},
	],
	scrollBehavior(to, from, savedPosition) {
		// 
		return { y: 0 };
	},
});

// 
router.beforeEach(async (to, from, next) => {
	//  token
	let token = store.state.login.token;
	// 
	let name = store.state.login.userInfo.name;
	//   token
	if (token) {
		//  login  register 
		if (to.path === '/login' || to.path === '/register') {
			next('/home');
		} else {
			// 
			if (name) {
				next();
			} else {
				try {
					//  actions 
					await store.dispatch('login/userInfo');
					next();
				} catch (error) {
					console.log(123);
					//  token  
					// 
					// 
					if (error.message === '208') {
						// token
						localStorage.removeItem('token');
						// 
						await store.dispatch('login/userLogOut');
						//  login
						next('/login');
					}
				}
			}
		}
	} else {
		//  
		//  pay trade paysuccess center
		if (to.path === '/pay' || to.path === '/trade' || to.path === '/paysuccess' || to.path === '/center') {
			next('/login');
		} else {
			next();
		}
	}
});

// 
router.afterEach(async (to, from) => {
	//   title
	let title = router.currentRoute.meta.title;
	if (title) {
		document.title = title;
	} else {
		//  detail  actions 
		if (to.name === 'detail') {
			document.title = '...';
			try {
				await store.dispatch('detail/getDetailList', router.currentRoute.params.skuId);
				//  title 
				document.title = store.state.detail.detailDataList.skuInfo.skuName;
			} catch (error) {}
		}
	}
});

// 
export default router;
