<template>
	<div class="cart">
		<h4></h4>
		<div class="cart-main">
			<div class="cart-th">
				<div class="cart-th1"></div>
				<div class="cart-th2"></div>
				<div class="cart-th3"></div>
				<div class="cart-th4"></div>
				<div class="cart-th5"></div>
				<div class="cart-th6"></div>
			</div>
			<div class="cart-body">
				<ul class="cart-list" v-for="item in cartInfoList" :key="item.id">
					<li class="cart-list-con1">
						<input type="checkbox" name="chk_list" :checked="item.isChecked === 1" @click="checkedStatus($event, item.skuId)" />
					</li>
					<li class="cart-list-con2">
						<img :src="item.imgUrl" />
						<div class="item-msg">{{ item.skuName }}</div>
					</li>
					<li class="cart-list-con3">
						<div class="item-txt">{{ item.sourceType }}</div>
					</li>
					<li class="cart-list-con4">
						<span class="price">{{ item.skuPrice }}</span>
					</li>
					<li class="cart-list-con5">
						<a href="javascript:void(0)" class="mins" @click="changeSkuNum(item.skuNum, item.skuId, -1)">-</a>
						<input autocomplete="off" type="text" :value="item.skuNum" minnum="1" class="itxt" readonly="readonly" />
						<a href="javascript:void(0)" class="plus" @click="changeSkuNum(item.skuNum, item.skuId, 1)">+</a>
					</li>
					<li class="cart-list-con6">
						<span class="sum">{{ item.skuNum * item.skuPrice }}</span>
					</li>
					<li class="cart-list-con7">
						<a href="javascript:;" class="sindelet" @click="removeCartDataList(item.skuId)"></a>
						<br />
						<a href="javascript:;"></a>
					</li>
				</ul>
			</div>
		</div>
		<div class="cart-tool">
			<div class="select-all">
				<input class="chooseAll" type="checkbox" :checked="isAllChecked" @click="SelectAllChecked($event)" />
				<span></span>
			</div>
			<div class="option">
				<a href="javascript:;" @click="removeSelected"></a>
				<a href="javascript:;"></a>
				<a href="javascript:;"></a>
			</div>
			<div class="money-box">
				<div class="chosed">
					 <span>{{ judgmentChecked }}</span
					>
				</div>
				<div class="sumprice">
					<em> </em>
					<i class="summoney">{{ cartPrice }}</i>
				</div>
				<div class="sumbtn">
					<!-- <router-link to="/trade" class="sum-btn"></router-link> -->
					<a href="javascript:;" class="sum-btn" @click="settlement"></a>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import throttle from 'lodash/throttle';
import { mapGetters } from 'vuex';
export default {
	name: 'ShoppingCart',
	computed: {
		...mapGetters('cart', ['cartDataList']),
		// 
		cartInfoList() {
			return this.cartDataList.cartInfoList || [];
		},
		// 
		cartPrice() {
			// 
			let price = 0;
			this.cartInfoList.forEach(e => {
				if (e.isChecked === 1) {
					// = * 
					price += e.skuNum * e.skuPrice;
				}
			});
			return price;
		},
		// 
		isAllChecked() {
			//  isChecked 1
			return this.cartInfoList.every(e => e.isChecked === 1);
		},
		// 
		judgmentChecked() {
			// 
			let num = 0;
			this.cartInfoList.forEach(element => {
				if (element.isChecked === 1) {
					num += element.skuNum;
				}
			});
			return num;
		},
	},
	methods: {
		// actions
		getCartDataList() {
			this.$store.dispatch('cart/getCartDataList');
		},
		// or
		//  
		changeSkuNum: throttle(async function (minNum, id, num) {
			// 
			if (num === -1) {
				if (minNum === 1) {
					return;
				}
			}
			// Promiseor
			try {
				await this.$store.dispatch('detail/addCart', { skuId: id, skuNum: num });
				this.getCartDataList();
				this.$message({
					message: ``,
					type: 'success',
				});
			} catch (error) {
				this.$message({
					message: ',',
					type: 'error',
				});
			}
		}, 1000),
		// 
		removeCartDataList(skuId) {
			// 
			this.$confirm(', ?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning',
			})
				.then(async () => {
					// promise
					try {
						await this.$store.dispatch('cart/removeCartList', skuId);
						this.getCartDataList();
						this.$message({
							type: 'success',
							message: '!',
						});
					} catch (error) {
						this.$message({
							message: '',
							type: 'error',
						});
					}
				})
				.catch(() => {
					this.$message({
						type: 'info',
						message: '',
					});
				});
		},
		// 
		async checkedStatus(e, skuId) {
			try {
				let isChecked = e.target.checked ? 1 : 0;
				await this.$store.dispatch('cart/upDataChecked', { skuId, isChecked });
				// 
				this.getCartDataList();
				this.$message({
					message: '',
					type: 'success',
				});
			} catch (error) {
				this.$message({
					message: '',
					type: 'error',
				});
			}
		},
		// 
		async SelectAllChecked(e) {
			try {
				let isChecked = e.target.checked ? 1 : 0;
				for (let i = 0; i < this.cartInfoList.length; i++) {
					await this.$store.dispatch('cart/upDataChecked', { skuId: this.cartInfoList[i].skuId, isChecked });
				}
				// 
				this.getCartDataList();
				this.$message({
					message: '',
					type: 'success',
				});
			} catch (error) {
				this.$message({
					message: '',
					type: 'error',
				});
			}
		},
		// 
		removeSelected() {
			this.$confirm(', ?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning',
			})
				.then(async () => {
					try {
						for (let i = 0; i < this.cartInfoList.length; i++) {
							if (this.cartInfoList[i].isChecked === 1) {
								await this.$store.dispatch('cart/removeCartList', this.cartInfoList[i].skuId);
							}
						}
						this.getCartDataList();
						this.$message({
							type: 'success',
							message: '!',
						});
					} catch (error) {
						this.$message({
							message: '',
							type: 'error',
						});
					}
				})
				.catch(() => {
					this.$message({
						type: 'info',
						message: '',
					});
				});
		},
		// 
		async settlement() {
			try {
				// 
				await this.$store.dispatch('trade/UserAddress');
				this.$router.push('/trade');
			} catch (error) {
				if (error.message === '208') {
					this.$confirm('', '', {
						confirmButtonText: '',
						cancelButtonText: '',
						type: 'warning',
					})
						.then(() => {
							this.$router.push('/login');
						})
						.catch(() => {
							this.$message({
								type: 'info',
								message: '',
							});
						});
				}
			}
		},
	},
	mounted() {
		// actions
		this.getCartDataList();
	},
};
</script>

